import { PrismLight as SyntaxHighlighter } from 'react-syntax-highlighter';
import bash from 'react-syntax-highlighter/dist/languages/prism/bash';
import prism from 'react-syntax-highlighter/dist/styles/prism/prism';

import withPostProps from '../../lib/with-post-props';
import Bonus from '../../components/Bonus';
import External from '../../components/External';
import Inline from '../../components/Inline';
import Post from '../../components/Post';

SyntaxHighlighter.registerLanguage('bash', bash);

const setupOut = `# Remove files from the out directory
$ rm -rf ./out/

# Exclude the out directory from Git
$ echo "out/" >> .gitignore
$ git add .
$ git commit -m "Ignore the out directory."

# Create an orphan branch named gh-pages
$ git checkout --orphan gh-pages

# Remove all staged files
$ git rm -rf .

# Create an empty commit (remember this is the gh-page branch)
$ git add .
$ git commit --allow-empty -m "Initial empty branch"

# Push the branch
$ git push origin gh-pages
`;

const setupGitWorkTree = `# Go back to master
$ git checkout master

# Create the Git Worktree
$ git worktree add out gh-pages`;

const updateGhPages = `# Build and generate an export
$ yarn build
$ yarn export

# Push the update site to GitHub pages
$ cd ./out
$ git add .
$ git commit -m "Deploy to gh-pages ðŸš€"
$ git push origin gh-pages
`;

export default withPostProps(({ post }) => (
    <Post post={ post }>
        <div className="constrained">
            <p>So, you've built an amazing Next.js site. But where do you host it?</p>
            <p>For free, on GitHub pages of course ðŸ˜€</p>
            <p>There's always a caveat... this only works if you can statically export your site. <External href="https://nextjs.org/docs/#static-html-export">Read more about it on next.js</External>. If you can, then you're good to go.</p>
            <p><External href="https://nextjs.org/docs/#usage">Follow the steps</External> to create <Inline>exportPathMap</Inline> in <Inline>next.config.js</Inline> add <Inline>next build</Inline> and <Inline>next export</Inline> scripts to <Inline>package.json</Inline>.</p>
            <p>You should have an <Inline>./out</Inline> directory with your entire website in it.</p>
            <Bonus>I use the <Inline>serve</Inline> module to test the <Inline>out</Inline> directory has everything it needs. Make sure you test loading specific URLs to ensure a direct page load works (versus client side navigation).</Bonus>
            <p>Now comes the tricky part... setting up GitHub pages.</p>
            <p>Actually, it's not too confusing unless you want to set up a custom domain. I found their documentation mind boggling; due to the fact there are quite a few different types of GitHub repositories including organisation, user and projects.</p>
            <p>Here is how it's going to work.</p>
            <p>We'll exclude the <Inline>out</Inline> directory from Git. We'll then use a <External href="https://git-scm.com/docs/git-worktree">Git Worktree</External> to create a kind of alias between the <Inline>out</Inline> directory and the <Inline>gh-pages</Inline> branch.</p>
            <p>Okay, let's get started by preparing the <Inline>out</Inline> directory:</p>
            <SyntaxHighlighter language="shell" style={ prism }>{ setupOut }</SyntaxHighlighter>
            <p>Now we can create the Git worktree:</p>
            <SyntaxHighlighter language="shell" style={ prism }>{ setupGitWorkTree }</SyntaxHighlighter>
            <p>Let's take a moment here to explore this...</p>
            <p>If you run <Inline>git branch</Inline> you should be on <Inline>master</Inline>. Now change into the <Inline>out</Inline> directory and run <Inline>git branch</Inline>, you should be on the <Inline>gh-pages</Inline> branch. So the <Inline>out</Inline> directory has been directly aliased to the <Inline>gh-pages</Inline> directory. That's kinda neat.</p>
            <p>Do the following every time you want to deploy a new version of the site:</p>
            <SyntaxHighlighter language="shell" style={ prism }>{ updateGhPages }</SyntaxHighlighter>
        </div>
    </Post>
));